/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Component" }; // TODO: adjust namespace

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { externalRequest, accountNumber } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    // const cart = typeof externalRequest.cart ? externalRequest.cart : "";
    return (
      <Card title={t("Component")}>
        <div>test</div>
      </Card>
    );
  };

  // const getCart = (cart) => {
  //   const data = { customer: accountNumber, cart: cart };

  //   sendRequest({ req: "get cart", data: data });
  // };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
